(function() {
  'use strict';

  angular
    .module('glass')
    .controller('ResultDialogController', ResultDialogController);

  /** @ngInject */
  function ResultDialogController($mdDialog, moment) {
    var vm = this;
    vm.cancel = cancel;
    vm.prettify = prettify;

    vm.options = {
      attrs: {
        insert: { class: 'insertion' },
        delete: { class: 'deletion' }        
      }
    };

    vm.title = vm.result.description + ' from ' + vm.result.institution;
    vm.title += ', unit ' + vm.result.unit + ' (performed ' + prettify(vm.result.timestamp) + ')';
    
    function cancel() {
      $mdDialog.cancel();
    }
    
    function prettify(timestamp) {
      return moment(timestamp).format('M/D/YYYY, HH:mm');
    }
    
  }

})();
(function() {
  'use strict';

  angular
    .module('glass')
    .directive('dynamicHeight', dynamicHeight);

  /** @ngInject */
  function dynamicHeight() {
    
    var directive = {
      restrict: 'A',
      require: '^mdVirtualRepeatContainer',
      link: link
    };
        
    function link(scope, element, attributes, mdVirtualRepeatContainer) {
      
      angular.element(element).css('height', element[0].parentNode.clientHeight + 'px');
      scope.$watch(function () {
        return element[0].parentNode.clientHeight;
      }, function(value){
        angular.element(element).css('height', value + 'px');
        mdVirtualRepeatContainer.setSize_(value);
      });
    }
    
    return directive;
    
  }

})();
